form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  background-color: #c6e7f2;  
  padding: 20px;
  border-radius: 10px;

  h1 {
    font-size: 2rem;
    margin-bottom: 30px;
  }

  label {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }

  input {
    padding: 8px;
    font-size: 1.2rem;
    border-radius: 4px;
    border: 1px solid #ccc;
    margin-bottom: 20px;
    width: 100%;
    box-sizing: border-box;

    &:focus {
      outline: none;
      border-color: #6bcbef;
    }
  }

  .hikes_button {
    background-color: #6befa2;
    color: white;
    font-size: 1.2rem;
    padding: 8px 16px;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;

    &:hover {
      background-color: #2ea546;
    }
  }

  @media screen and (max-width: 768px) {
    form {
      padding: 10px;
    }
  }

  @media screen and (max-width: 480px) {
    form {
      padding: 5px;
    }
  }
}
